import { createWebHashHistory, createRouter } from "vue-router";
// import { createWebHistory, createRouter } from "vue-router";
import List from './components/MyList.vue';
import Happy from './components/MyHappy.vue';
import Error from './components/MyError.vue';
import Detail from './components/MyDetail.vue';
import Home from './components/MyHome.vue';
import Video from './components/MyVideo.vue';
import DDay from './components/DDay.vue';

const routes = [
      { 
         path: "//:id(\\d+)", 
         component: Detail,
      },
      { 
        path: "/list", 
        component: List,
      },
      {
        path: "/happy",
        component: Happy,
      },
      { 
      path: "//home", 
      component: Home,
      },
      { 
      path: "//video", 
      component: Video,
      },
      { 
        path: "//DDay", 
        component: DDay,
      },     
      {
        path: "/:anything(.*)",
        component: Error,
      }

      
//   {
//     path: "/ê²½ë¡œ",
//     component: importí•´ì˜¨ ì»´í¬ë„ŒíŠ¸,
//   }
];

// const router = createRouter({
//   history: createWebHistory(),
//   routes,
// });

const router = createRouter({
   history: createWebHashHistory(),
   routes,
 });

export default router; 